import Vue from 'vue'
import Router from 'vue-router'
import write from '@/router/modules/write'
import post from '@/router/modules/post'
import single from '@/router/modules/single'
import homePage from '@/router/modules/homePage'
import seeOcean from '@/router/modules/seeOcean'
import articleDetails from '@/router/modules/articleDetails'
import rendezvousPage from '@/router/modules/rendezvousPage'
import forumPage from '@/router/modules/forumPage'
import personalHomePage from '@/router/modules/personalHomePage'

Vue.use(Router)

export default new Router({
    routes: [{
            path: '/',
            name: 'index',
            component: resolve => require(['@/views/homepage/homePage.vue'], resolve)
        },
        ...write,
        ...post,
        ...single,
        ...homePage,
        ...write,
        ...seeOcean,
        ...articleDetails,
        ...rendezvousPage,
        ...forumPage,
        ...personalHomePage
    ]
})


