import axios from 'axios'
import port from '@/assets/js/port'
import ImageCompressor from 'image-compressor.js'

let compressorImage = file => {
  return new Promise((resolve, reject) => {
    new ImageCompressor(file, {
      quality: 0.8,
      maxWidth: 750,
      convertSize: 2000000,
      success: result => {
        resolve(result)
      },
      error: err => {
        console.log('图片压缩失败')
        reject(err)
      },
    })
  })
}

let uploadImage = file => {
  return new Promise((resolve, reject) => {
    compressorImage(file).then(res => {
      let formData = new FormData()
      formData.append('imgType', 1)
      formData.append('upload', res, res.name)
      axios.post(port.UPLOAD_IMG, formData).then(res => {
        if (!res.code === 200) {
          console.log('图片上传失败')
        }
        resolve(res)
      }).catch(err => {
        console.log('图片上传失败')
        reject(err)
      })
    })
  })
}
export default {
  compressorImage,
  uploadImage,
}



